* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Merriweather', sans-serif;
    
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar {
	padding-top: 15px;
	padding-bottom: 15px;
}

ul {
	list-style: none;
}

.nav-link {
 cursor: pointer;
}

.nav-link {
 margin-left: 3em !important;
 color: black;
 font-weight: bold;
}

.nav-item > .active {
    border-bottom: 1px solid #F97300;
}




.about { 
    display: flex;
    margin: 2em 4em 4em;
    padding: 1em;
    position: relative; 
}

.about-img-wrap {
    width: 270px;
    height: 378px;
    margin-top: 4em;
    order: 2;
}

.about-img-wrap .about-img {
    position: relative;
}

.about-img-wrap img {
    margin-left: 2em;

}   
.about-me {
    order: 1;
    width: 510px;
    padding-top: 4em;
}

.about-me-text {
    font-size: 16px;
    line-height: 1.5;
    color:#767676;
}

.about-me-title {
    font-size: 16px;
    line-height: 1.5;
    color:#F97300;  
    margin-bottom: 1em;  
}

.about-me-info {
    margin-top: 190px;
}

.span-title {
    color:#F97300;
    font-weight: bold;
    float: left;
    display: block;
}

.span-icons a {
    color: #000;
    
}

.span-icons a:last-child {
    margin-left: 1em;
} 

.span-title + span {
    display: block;
    padding-left: 80px;
    opacity: 0.8;
}

.contact-me-btn {
    margin-top: 2em;
}

.contact-me-btn a{   
    font-size: 12px;
    font-weight: bold;
    background:#3333ff;
    border: 0;
    border-radius: 2px;
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
}

.contact-me-btn a:hover {
    box-shadow: 0 0 5px #3333ff;
    cursor: pointer;
} 
.site-btn {
    padding: 15px;
}

@media (max-width: 991px) {

    .about-me {
        width: 310px;
    }
    
    .about-img-wrap {
        height: 250px;
    }
         
    .about-me-info {
        margin-top: 70px;
    }
}

@media (max-width: 767px) {

    .about-img-wrap {
        text-align: center;
        order: -1;
    } 
}
.description {
    margin: 2em 4em 4em;
    padding: 1em;
    position: relative;  
   }

.description h3 {
    color:#F97300;
    margin-bottom: 1em;
}

.description dt {
    margin-bottom: 2em;
}

.description dd {
    color:#767676;
}

@media (max-width: 767px) {
    .description dt {
        margin-top: 1em;
    }
}

.education {
    margin: 2em 4em 4em;
    padding: 1em;
    position: relative;

   }

.education-title {
    color: #F97300;
    margin-bottom: 1em; 
}

.education dt {
    height: 60px;
    margin-bottom: 1em;
}


.education dd {
    color:#767676;
}

@media (max-width: 991px) {
    .education-about {
        margin-top: 5em;
    }
    
    .education dl {
        width: 190px;
    }
}

@media (max-width: 767px) {
    .education dt {
        margin-top: 1em;
    }

    .education dd {
        margin-bottom: 1em;
    }
}


.portfolio {
    margin: 2em 4em 4em;
    padding: 1em;
    position: relative;
   }

.portfolio h3 {
    color: #F97300;
    margin-bottom: 1em; 
}

.portfolio-scale {
    display: inline-block; 
    overflow: hidden; 
}

.portfolio-scale img {
    transition: 0.3s; 
    display: block; 
}

.portfolio-scale img:hover {
    transform: scale(1.2); 
}

.portfolio-box {
    display: block;
    margin: 0 auto 30px;
    text-align: left;
    position: relative;
    overflow: hidden;
    height: auto;
}

.contact {
    height: 100vh;
    margin: 2em 4em 4em;
    padding: 1em;
    position: relative;
}

.contact-title h3 {
    color: #F97300;
}

.contact-text {
    text-align: left;
    display: inline-block;
    max-width: 400px;
    margin-top: 10em;
}

.contact-form {
    padding: 2em 1px;
    max-width: 340px;
    float: right;
    margin-top: 5em;
    margin-right: 20em;
}

.contact .contact-form .btn-secondary {
    background: #F97300;
    color: #fff;
    border: 0;
}

.form-control {
    margin-top: 5px;

}

.contact .contact-form .form-control::-webkit-input-placeholder {
    color: #888;
    font-size: 16px;
}

.contact .contact-form .form-control:-ms-input-placeholder {
    color: #888;
    font-size: 16px;
}

.contact .contact-form .form-control::-ms-input-placeholder {
    color: #888;
    font-size: 16px;
}

.contact .contact-form .form-control::placeholder {
    color: #888;
    font-size: 16px;
}

.msg {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 10px;
    padding: 3px;
    color: #17A05D;
    z-index: 1;
    background-color: #fff;
    display: none;
    font-family: "Open Sans", sans-serif;
}

.msgAppear {
    display: block;
}

@media (max-width: 767px) {
    .contact-about {
        width: 260px;
    }
}
@media (max-width: 991px) {
    .contact-text {
        margin-top: 2em;
    }

    .contact-form {
        margin-top: 2em;
        float: none;
        text-align: center;
        width: 100%;
    }
}

@media (max-width: 1125px) {
    .contact-form {
        margin-right: 5em;
    }
}

